.slick-dots li {
    position: relative;
    display: inline-block;
    width: 9px !important;
    height: 20px !important;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;
}
.justify-content-between {
    justify-content: space-around !important;
}
.slick-dots li button:before {
    font-family: 'slick';
    font-size: 2px;
    line-height: 20px;
    position: absolute;
    top: 0;
    left: 0;
    width: 12px !important;
    height: 12px !important;
    content: '';
    text-align: center;
    opacity: .25;
    color: black;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.slick-dots{
    margin: 0px !important;
    padding: 0px !important;
}
.slick-dots li {
    position: relative;
    /* display: inline-block; */
    width: 20px;
    height: 20px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;
    left: 0px !important;
}
.oval:before {
    content: "";
    position: absolute;
    top: 100%;
    left: 0%;
    transform: translate(0%, -100%);
    width: 0;
    height: 0;
    border-radius: 200px;
    background: linear-gradient(0deg, #3A76BB 5.97%, rgba(58, 118, 187, 0) 68.87%), #2E3192;
    opacity: 0;
    transition: all 1.2s;
}
.oval:hover h1{
    color:#fff;
    position: absolute;
    left: 41%;
}
.oval:hover p{
    color:#fff;
    position: absolute;
    top: 84px;
    left: 10px;
}
.oval:hover {
    transform: translateY(0%);
    color: #fff;
}
.oval:hover::before {
    width: 225px;
    height: 221px;
    opacity: 1;
    transition: all 1.2s;
}


