/* custome select tag arrow  */
  .custom-select-wrapper {
    position: relative;
    display: inline-block;
    width: 100%;
  }
  
  .custom-select {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 100%;
  }
  
  
  
  .arrow-image {
    position: absolute;
    top: 51%;
    right: 17px;
    transform: translateY(-50%);
    pointer-events: none;
    width: 12px;
    height: 7px;
  }
  
  /* End custome select tag arrow  */

