@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Font+Awesome+6+Free:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=FontAwesome:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Font+Awesome+6+Brands:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-nunito-sans: "Nunito Sans";
  --text-md-medium: Inter;
  --font-poppins: Poppins;
  --font-nunito: Nunito;
  --font-fontawesome: FontAwesome;
  --font-font-awesome-6-brands: "Font Awesome 6 Brands";
  --font-open-sans: "Open Sans";
  --font-font-awesome-6-free: "Font Awesome 6 Free";

  /* font sizes */
  --font-size-mid: 17px;
  --text-md-medium-size: 16px;
  --font-size-11xl: 30px;
  --font-size-xl: 20px;
  --font-size-26xl: 45px;
  --font-size-231xl: 250px;
  --font-size-lg: 18px;
  --font-size-2xs: 11px;
  --font-size-3xs: 10px;
  --font-size-8xs: 5px;
  --font-size-xs: 12px;
  --font-size-3xl: 22px;
  --font-size-51xl: 70px;

  /* Colors */
  --color-white: #fff;
  --text-light: #fbfbfb;
  --color-gray-300: #858585;
  --color-gray-100: #818181;
  --color-gray-400: #292929;
  --color-gray-200: #1e1e1e;
  --color-gray-500: #1d1d1d;
  --color-lightcyan: #cff1ff;
  --color-black: #000;
  --color-steelblue: #3a76bb;
  --color-midnightblue-100: #2e3192;
  --color-midnightblue: #2e3092;
  --color-cornflowerblue: #5486e9;
  --color-darkslategray-100: #434343;
  --color-darkslategray-200: #373737;
  --color-darkslategray-300: #2e2e2e;
  --color-dimgray-100: #5f5f5f;
  --color-dimgray-200: #58595a;
  --color-lightgray: #cdcdcd;
  --color-aliceblue-200: #f2f8ff;
  --color-aliceblue-100: #ecf8ff;
  --color-gold: #ffc700;
  --color-gainsboro-100: #e8e8e8;
  --color-gainsboro-200: #dcdcdc;
  --color-deepskyblue: #00b7ff;

  /* Gaps */
  --gap-5xs: 8px;

  /* Paddings */
  --padding-3xs: 10px;
  --padding-21xl: 40px;
  --padding-xs: 12px;
  --padding-xl: 20px;

  /* border radiuses */
  --br-81xl: 100px;
  --br-3xs: 10px;
  --br-mini: 15px;
  --br-31xl: 50px;

  /* Effects */
  --shadow-xs: 0px 1px 2px rgba(16, 24, 40, 0.05);
}
