.getapp{
    margin: 100px 0px 31px 0px;
    margin: 77px 0px 31px 0px;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.2) 0%, rgba(46, 49, 146, 0.2) 46.16%, rgba(97, 156, 224, 0.2) 77.25%, rgba(58, 118, 187, 0.2) 100%), #2E3192;
    color: #fff;
    padding: 49px 0px 0px;
    height: 338px;
}
.getapp_imges {
    width: 100%;
    transform: translate(-31px, 5px);
}
.expert_main{
    text-align: center;
}
.expert_main h1{
    font-family: 'Poppins';
font-style: normal;
font-weight: 600;
font-size: 34px;
line-height: 45px;
text-align: center !important;
color: #FFFFFF;
}
.expert_main p{
    font-family: 'Nunito Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 27px;
    text-align: center;
    color: #FFFFFF;
}

.getapp_img {
    width: 268px;
    transform: translate(55px, -22px);
}
.inner_section h1{
    font-family: 'Poppins';
font-style: normal;
font-weight: 600;
font-size: 30px;
line-height: 40px;
text-align: center !important;
color: #000000;
text-align: center;
}
.inner_section p{
    font-family: 'Nunito Sans';
font-style: normal;
font-weight: 400;
font-size: 17px;
line-height: 28px;
text-align: center;
color: #5F5F5F;
text-align: center;
}
.inner_section  span{
    color:#3A76BB;

}
.selector {
    display: flex;
    background: #E6F1FF;
    border-radius: 15px;
    padding: 24px 24px 0px 24px;
    margin: 12px 0px;
}
.selector > .selector_img{
    width: 33%;
}
.selector_section + p{
    text-align: left;
}
.selector > .selector_section{
    width: 66%;
    padding: 21px 0px 10px;
  
}
.selector > .selector_section > .selector_inner--section >  span {
    padding: 5px 33px;
    background: #FFFFFF;
border-radius: 50px;
font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 17px;
line-height: 28px;
text-transform: uppercase;
color: #3A76BB;
}
.selector > .selector_section h1{
    font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 25px;
line-height: 28px;
color: #2E3092;
text-align: left;
margin: 12px 0px 5px;
}
.user_location{
    width:100%;
    height: auto !important;
}
.selector_section .selector_inner--section > p{
    font-family: 'Nunito Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 28px;
    text-align: left;
color:#373737;
}
.selector_inner--section {
    width: 300px;
    margin: auto;
}
.getapp_section{
    position: relative;
    background-color: var(--color-white);
    width: 100%;
    /* height: 4316px; */
    overflow: hidden;
    text-align: left;
    font-size: var(--font-size-51xl);
    color: var(--color-white);
    font-family: var(--font-poppins);
    z-index: -1;
}
.getapp_section .mobile-mockup-1-icon {
    position: absolute;
    top: 21%;
    left: calc(37% - 450px);
    width: auto;
    height: 478px;
    border-radius: 25px;
}
.getapp_section .mobile-user-1-icon {
    position: absolute;
    top: 16%;
    left: 59%;
    width: 31%;
    /* height: 524.25px; */
    object-fit: cover;
}
.getapp_section  .btns-1-icon {
    position: absolute;
    top: 70%;
    left: 30.8%;
    width: 22%;
    /* height: 60px; */
    object-fit: cover;
}
.inner_section{
    margin: 68px 0px 40px;
}
@media (max-width: 1280px) and (min-width: 1180px) {
    .selector > .selector_section {
        width: 66%;
        padding: 0px 0px 10px !important;
      }
      .selector_section .selector_inner--section > p {
        font-family: 'Nunito Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 17px;
        line-height: 28px;
        text-align: left;
        color: #373737;
        margin: 0px;
    }
}